
import { defineComponent } from 'vue';
import { mapActions, mapGetters } from 'vuex';
import BookCartBar from '../components/BookCartBar.vue';
import TheLoader from '../components/TheLoader.vue';
import ButtonBrown from '../ui/ButtonBrown.vue';

interface booksType {
  author: string,
  authorId: string,
  title: string,
  price: number,
  image: string,
  key: string,
  discount: number | null,
  quantityInCart: number
}

export default defineComponent({
  name: 'CartView',
  data(){
    return {
      loadContent: true
    }
  },
  components: {
    TheLoader,
    BookCartBar,
    ButtonBrown
  },
  computed: {
    ...mapGetters([
      'cartBooksList',
      'cartSize',
      'totalCartPrice'
    ]),
    emptyCart(){
      return !this.cartSize;
    }
  },
  watch: {
    cartBooksList(){
      this.loadContent = false;
    },
  },
  methods: {
    ...mapActions([
      'getCartBooks'
    ])
  },
  mounted(){
    if(!this.cartSize){
      this.loadContent = false;
    } else {
      this.getCartBooks();
    }
  }
});
